.sortable-container {
    margin: auto;
    padding: 0px;
    font: var(--font);
}

.sortable-component.vertical {
    display: flex;
    flex-wrap:wrap;
    justify-content: flex-start;
    align-items: stretch;
}

.sortable-component.vertical .sortable-container {
    min-width: 175px;
    margin: 0px;
    padding: 10px;
    flex-grow: 1;
}

.sortable-container-header {
    margin: 0px;
    background-color: var(--background-color);
}

.sortable-container-body {
    margin: 0px;
    padding: 3px;
    width: 100%;
    min-height: 48px;
    border-radius: 3px;
    background-color: var(--secondary-background-color);
}

.sortable-item, .sortable-item:hover {
    margin: 5px;
    background-color: var(--primary-color);
    color: white;
    padding-top: 3px;
    padding-bottom: 3px;
    height: 100%;
    cursor: grab;
    user-select: none;
}

.sortable-item:active {
    cursor: grabbing;
}

.active {
    opacity: 0.5;
}

.sortable-item.dragging {
    transform-origin: 0 0;
}

.sortable-component.vertical .sortable-item {
    display: block;
}
.drag-handle {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: grab;
  user-select: none;
  padding: 0 8px 0 2px;
  opacity: 0.7;
}

.drag-handle:hover {
  opacity: 1;
}

.item-content {
  cursor: pointer;
  width: 100%;
}
